import React from 'react'
function SEO({title}:{title:string}) {
  return (
    <>   
        <meta charSet="UTF-8"/>
    <meta httpEquiv="X-UA-Compatible" content="IE=edge"/>
    <meta name="viewport" content="width=device-width, initial-scale=1.0"/>
    <meta name='description' content="قران كريم بكامل السور بصوت المنشاوي دعونا نتشارك الاجر في سماع القران والعمل به ونشره" />
   
    <title >{title}</title>
    </>
  )
}

export default SEO