import React, { Component } from 'react'
import AudioContext from '../context/audioContext'
import PlayArrowIcon from '@mui/icons-material/PlayArrow';
import { ArrowCircleLeft, ArrowCircleRight, ArrowLeft, ArrowLeftOutlined, Pause } from '@mui/icons-material';
import { IconButton ,Slider} from '@mui/material';
import {Link, useParams} from 'react-router-dom'
import SEO from '../SEO/seo';


type Props ={
  params:{
    id:String
  }
}


type State = {
    sliderValue:number,
    currentTime:number,
    duration:number
}

class PlayerPage extends Component<Props ,State>  {

    static contextType=  AudioContext;
    context!:React.ContextType<typeof AudioContext>;

    constructor(props:any){
        super(props);

        this.state={
          sliderValue:0,
          currentTime:0,
          duration:0
        }
    }
    componentDidMount(){
      

      document.body.style.overflowY = 'hidden'
      this.setState({
        currentTime:this.context.audio.currentTime,
        duration:this.context.audio.duration
      })
    
    }

    componentDidUpdate(prevProps:any,prevState:any){
      if(!this.context.audio.currentTime || !this.context.audio.duration)return
      let currentTime = this.state.currentTime;
      let duration = this.state.duration;
      if(this.state.currentTime !== prevState.currentTime){
      this.setState({
        sliderValue: (currentTime/duration)*100
      })

    }
    if(this.state.currentTime !== this.context.audio.currentTime){
    this.setState({
      currentTime: this.context.audio.currentTime
    })
  }
    
    if(this.state.duration !== this.context.audio.duration){
    this.setState({
      duration: this.context.audio.duration
    })

  }

    
    }
  
 
    componentWillUnmount(){
      document.body.style.overflowY = "auto";

    }
  

    sliderChange(e:any,value:any){

      if(!this.context.audio.duration)return
      this.setState({
        sliderValue:value
      });
      this.context.setAudio((old:any)=>{
        return{
          ...old,
        paused:true
      }
      })
     

    }

    sliderChangeDone(e:any,value:any){
      if(!this.context.audio.duration)return

 this.context.setAudio({
        ...this.context.audio,
        newCurrentTime:( (value/100) * this.context.audio.duration),
        currentTime:( (value/100) * this.context.audio.duration),
        paused:false
      })

    }

    
    toTime(time:string){

      if(isNaN(Number(time)) || Number(time)<0)return "";
      let sec_num = parseInt(time, 10); // don't forget the second param
      let hours:any   = Math.floor(sec_num / 3600);
      let minutes:any = Math.floor((sec_num - (hours * 3600)) / 60);
      let seconds:any = sec_num - (hours * 3600) - (minutes * 60);
  
      if (hours   < 10) {hours = String("0"+hours)}
      if (minutes < 10) {minutes = String("0"+minutes);}
      if (seconds < 10) {seconds = String("0"+seconds);}
      return hours+':'+minutes+':'+seconds;
  }

  render() {


    return (

      <div className='player-lg'>
        <Link to='/'>
        <ArrowCircleLeft className='icon-back'/>
        </Link>
 <SEO title={this.context.audio.data[this.context.audio.target]?.filename}/>
        <div className='container-image'>
          <img src={'/assets/mnsahwi.webp'} />
          <span>{this.context.audio.data[this.context.audio.target]?.filename}</span>
          </div>

         
        <div className="container-controls">
            <div className='time'>
            <span className="duration">{this.toTime(String(this.context.audio.currentTime))}</span>
            <span className="duration">{this.toTime(""+this.context.audio.duration )}</span>
            
            </div>
          <Slider onChangeCommitted={(e:any,value:any)=>this.sliderChangeDone(e,value)} onChange={(e:any,value:any)=>this.sliderChange(e,value)} value={this.state.sliderValue} style={{width:'min(300px,90%)'}} />
            <div className='controls'>

          <IconButton onClick ={()=>this.prev()} >
            <ArrowCircleLeft className='icon ' />
          </IconButton>
          <IconButton onClick={() => this.play()}>

            {this.context.audio.paused ? <PlayArrowIcon className='icon ' /> :
              <Pause className='icon ' />}
          </IconButton>

          <IconButton onClick={()=>this.next()}>

            <ArrowCircleRight className='icon i' />
          </IconButton>


            </div>
            </div>
      </div>    
    )
  }

  updateURL(id:number){
    window.history.pushState([],"","/player/"+id);
  }

  next(){

    if(this.context.audio?.data[this.context.audio.target+1]){

    this.context.setAudio({
        ...this.context.audio,
        target:this.context.audio.target+1,
        paused:true

    })
    this.setState({...this.state,
sliderValue:0
    })

    this.updateURL(this.context.audio.target+2)
  }
  }

  prev(){
    if(this.context.audio?.data[this.context.audio.target-1]){

    this.context.setAudio({
        ...this.context.audio,
        target:this.context.audio.target-1,
        paused:true,
      
    })

    this.setState({...this.state,
      sliderValue:0
    })
    this.updateURL(this.context.audio.target)

  }

  }

  play(){
      if (this.context.audio.paused&& this.context.audioRef) {
    this.context.setAudio({
      ...this.context.audio,
      paused:false,
 
    })
    this.context.audioRef.current.play()


      } else {
        this.context.setAudio({
          ...this.context.audio,
          paused:true
        })
    this.context.audioRef.current.pause()

      
      }

  }
}




function withParam(Component:React.ComponentClass){
  
  return (props:any)=><Component {...props} params={useParams()} />;
}


export default withParam(PlayerPage)