import React, { useEffect, useRef ,useState} from 'react';
import './styles/variables.css'
import './styles/colors.css'
import './styles/style.css'
import {BrowserRouter as Router,Routes,Route, useNavigate} from 'react-router-dom'
import Home from './pages/Home';
import PlayerPage from './pages/PlayerPage';
import AudioContext from './context/audioContext';





function App() {
  const [audio,setAudio] = useState({data:[{filename:'',source:''}],target:0,paused:true,currentTime:0,newCurrentTime:0,duration:0});
  const audioRef = useRef()
  let navigate =useNavigate()
  function timeUpdate(e){
    if(e.target.currentTime >= e.target.duration){
      if(audio.data[audio.target+1])setAudio({...audio,target:audio.target+1});
      return
    }
    setAudio({
      ...audio,
      currentTime:e.target.currentTime
    })
  }


  useEffect(()=>{
 setAudio({
      ...audio,
      duration:audioRef.current.duration,
      currentTime:audioRef.current.currentTime
    })

  },[audioRef?.current?.duration])


  useEffect(()=>{
    if(audio.newCurrentTime <= 0 )return;

    audioRef.current.currentTime = audio.newCurrentTime;
  },[audio.newCurrentTime])


 return(
  <>
  <AudioContext.Provider value={{audio,setAudio:setAudio,audioRef}}>
  <Routes>
    <Route path='/'  element={<Home/>} >
    <Route  element={<PlayerPage/>} path='/player/:id' />
</Route>

<Route path="*" element={<Redirect/>}></Route>
  </Routes>
  <audio  onTimeUpdate={timeUpdate} src={audio?.data[audio.target]?.source}  hidden  ref={audioRef} />
  </AudioContext.Provider>
  </>
 )
}


function Redirect(){
  let navigate =useNavigate()
  useEffect(()=>{
    navigate('/')
  },[])
  return null
}
export default App;
