import React, { createContext } from "react";

interface audioType {
    filename:string,
    source:string
}

type contextType ={
    audio:{
        data:audioType[],
        target:number,
        currentTime:number,
        duration:number,
        paused?:Boolean,
        newCurrentTime?:number
    },
    setAudio:any,
    audioRef:any

}

const AudioContext = createContext<contextType>({
    audio:{data:[],target:-1,currentTime:-1,duration:-1},
    setAudio:null,
    audioRef:null
});

export default AudioContext