import React, { createRef } from 'react'
import PlayArrowIcon from '@mui/icons-material/PlayArrow';
import { ArrowCircleLeft, ArrowCircleRight, Pause } from '@mui/icons-material';
import { IconButton } from '@mui/material';
import {Link} from 'react-router-dom';
import AudioContext from '../context/audioContext'

type audioType ={
  filename:string,
  source:string
}

interface Props  {
  data?: any,
  target: number,
} 
type State = {
  paused: Boolean,
  targetAudio:number,
}

class PlayerSmaller extends React.Component<Props, State>{
  static contextType=  AudioContext;

  context!:React.ContextType<typeof AudioContext>;

  constructor(props: Props) {
    super(props);
    this.state = {
      paused: false,
      targetAudio:this.props.target
    }
  }


 
  componentWillReceiveProps(nextprops:any){
       this.setState({
      ...this.state,
      targetAudio:nextprops.target
    })
  }

  


  next(){


    if(!this.props.data)return;
    if(this.props.data[this.state.targetAudio+1]){
      


    this.context.setAudio({
      ...this.context.audio,
      target:this.context.audio.target+1,
      paused:true

    })


  }
  }

  prev(){
    if(!this.props.data)return;

    if(this.props.data[this.state.targetAudio-1]){

      this.context.setAudio({
        ...this.context.audio,
        target:this.context.audio.target-1,
        paused:true
      })
      

  }

  }


  play() {
      if (this.context.audio.paused && this.context.audioRef) {
        this.context.setAudio({
          ...this.context.audio,
          paused:false
        })
        
        this.context.audioRef.current.play()
      } else {
 
        this.context.setAudio({
          ...this.context.audio,
          paused:true
        })
        this.context.audioRef.current.pause()


    }
  }



  

  render(): React.ReactNode {

    if(!this.props.data)return
    if (this.props.target<0 || !this.props.data) return


    return (
      <div dir='rtl' className='player'>

        <Link to={'/player/'+(this.state.targetAudio+1)} className='info'>
          <img style={{ width: '70px', objectFit: 'cover' }} src={'/assets/mnsahwi.webp'} />
          <h4 style={{ marginLeft: '1rem' }}>{this.props.data[this.state.targetAudio]?.filename}</h4>
        </Link>
        <div  className='controll'>
          
        <IconButton onClick={()=>this.next()}>

<ArrowCircleRight className='icon i' />
</IconButton>
          
          <IconButton onClick={() => this.play()}>

            {this.context.audio.paused ? <PlayArrowIcon className='icon ' /> :
              <Pause className='icon ' />}
          </IconButton>

          <IconButton onClick ={()=>this.prev()} >
            <ArrowCircleLeft className='icon ' />
          </IconButton>

        </div>
      </div>
    )
  }

}



function Player(props:{ data: audioType[],
  target: number,
  }) {
  return (
    <PlayerSmaller {...props}/>
  )
}

export default Player