enum actions {
        LOAD="LOADING",
        ERR="ERR",
        READY="READY" ,
        SEARCH="SEARCH",
        INSEARCH="INSEARCH"
}

export type dataInterface= {
    loading:Boolean,
    error:Object|null,

    data:[]

}

type actionType ={
    type:string,
    payload:any
}

export const initalState:dataInterface = 
    {
        loading:false,
        data:[],
        error:null,
    }


function dataReducer(state:dataInterface,action:actionType) {
 
   switch(action.type){
    case actions.LOAD:
            return {
                ...state,
                loading:true,
            }
        case actions.ERR:
        return {...state,loading:false,error:true,body:action.payload};
    case actions.READY:
        return {
            ...state,
            data:action.payload.map((item:any)=>{return{...item,include:true}}),
            loading:false,
            error:false,
        };
        
        case actions.SEARCH:
            return{
                ...state,
                data:action.payload
            }
               
        case actions.INSEARCH:
            return {
                ...state,
                search:false
            };

      default: 
      return state
   }
   
}

export  {dataReducer}