import React, { useEffect, useReducer ,useState,useContext, useCallback} from 'react';
import logo from './logo.svg';
import { Alert, AlertTitle, AppBar, Dialog, DialogContent, DialogTitle, Grid, IconButton, Paper, TextField, Typography } from '@mui/material';
import { Box } from '@mui/system';
import '../styles/variables.css'
import '../styles/colors.css'
import '../styles/style.css'
import ModeNightIcon from '@mui/icons-material/ModeNight';
import LightModeIcon from '@mui/icons-material/LightMode';
import {dataReducer, initalState} from '../reducer/dataReducer';
import axios from 'axios';
import Player from '../component/Player';
import AudioContext from '../context/audioContext'
import {Outlet, useParams} from 'react-router-dom'
import { Info } from '@mui/icons-material';
import SEO from '../SEO/seo';

function Home() {
  const [state,dispatch] = useReducer(dataReducer,initalState);
  const [theme,setTheme] = useState("dark");
  const [openDialog,setOpenDialog] = useState(false);
  const {id} = useParams()
  const {audio,setAudio} = useContext(AudioContext);

async function getData(){
    try{
      let {data} = await axios.get(`${process.env.PUBLIC_URL}/all`)
   
  
      setAudio({
        ...audio,
        data:data,
        target:id?.trim()? Number(id)-1 : audio.target
      })
      dispatch({
        type:"READY",
        payload:data
      });
     }catch(e){
      dispatch({
        type:"ERR",
        payload:"Your Have A slow Connection"
      });
      
     }
    }
    
  useEffect(()=>{

    dispatch({
      type:"LOADING",
      payload:null
    })

    getData();

    
  },[])

  function  searchFilter(value:string){
    let result = state.data.map((item:any):[]=>{
      return {...item,include:item.filename.includes(value)}
      
    });
    dispatch({
      type:"SEARCH",
      payload:result
    });

  }
 
  return (
    <>
<Outlet/>
 <SEO title='قران كريم بصوت المنشاوي'/>
      <AppBar dir='rtl' variant='elevation'  position='static' style={{padding:' 1rem'}} className={`navbar ${theme} `}>
        <div>
        <Typography variant='h3' component={'div'}>
          القران
        </Typography>
        <small style={{position:'absolute',fontSize:'.3em', color:'#ffaa22'}}>بصوت المنشاوي</small>
        </div>
        <div style={{display:'flex',alignItems:'center'}}>

        <IconButton  onClick={()=>setTheme(theme==='light'?'dark':'light')} >
          
         {theme === 'light' ? <ModeNightIcon className='icon' /> : <LightModeIcon className='icon'/>}
        </IconButton>
        <IconButton  onClick={()=>setOpenDialog(true)} >

        <Info className='icon-info'/>
        </IconButton>

<Dialog style={{cursor:'pointer'}} dir='rtl' open={openDialog} onClick={()=>setOpenDialog(false)}>
  <DialogTitle >عن التطبيق</DialogTitle>
  <DialogContent style={{textAlign:'center'}}>تطبيق لسماع القران بصوت المنشاوي رحمه الله<br/> وهو وقف لي ولوالدي ولجميع المسلمين
  <hr/>
  <span style={{textAlign:'center',opacity:.3,color:'#000'}}>اضغط في اي مكان للاغلاق</span>
  </DialogContent>

</Dialog>
        </div>
         

       </AppBar>
     
       <div className={`containerOfList ${theme}`}>
       {state.loading&&   <div>
        loading
      </div>}
      
          {state.error===true ? <Alert className='alert center' color='error'>
              <AlertTitle>You Have Error </AlertTitle>

            </Alert>:<></>
            } 
              <div className='search-container' style={{display:'flex',alignItems:'center',padding:'10px 20px',color:'var(--white)'}} dir='rtl'>
                <label>ابحث</label>
            <TextField onBlur={({target})=>(target.value.trim()==="")?dispatch({type:"INSEARCH",payload:null}):null}  onChange={({target})=>searchFilter(target.value)} className=' bg-white' color='info' variant='outlined' placeholder='اسم السورة'  />
            </div>
           
            {state.data  &&<div className='grid'  style={{marginTop:0,padding:'1 ',direction:'rtl'}}  >
              {state.data.map((item:any,idx:number)=>  (
                item.include &&  <Paper key={idx} onClick={()=>setAudio({
                    ...audio,
                    target:idx,
                  })} className='card' >
                    <img style={{width:'100%'}} src={'/assets/mnsahwi.webp'} />
                    <span style={{marginRight:'1.4em'}}>
                    {item.filename}
                    </span>

                  </Paper>
              ))}
             
             
              </div>}
              <Player target={audio.target} data={audio.data}/>


      </div>

    </>
  
  );
}

export default Home;
